import { useParams, Link, useLocation } from 'react-router-dom'
import { ArrowLeft, Eye, Building, Link as LinkIcon, TrendingUp, BarChart3, Target, Users, Mail, UserMinus, Crown } from 'lucide-react'
import { usePlan, usePlanUsers, useCompanyIdentity, useStrategicAnalysis, useAnalysisTools, useStrategies } from '../hooks/useApi'
import { useToast } from '../components/ui/Toast'
import LoadingSpinner from '../components/common/LoadingSpinner'
import IdentityEditor from '../components/plan/IdentityEditor'
import ValueChainEditor from '../components/plan/ValueChainEditor'
import SwotEditor from '../components/plan/SwotEditor'
import AnalysisToolsEditor from '../components/plan/AnalysisToolsEditor'
import StrategiesEditor from '../components/plan/StrategiesEditor'
import { useState } from 'react'

const PlanEditor = () => {
   const { planId } = useParams()
   const location = useLocation()
   const { data: plan, isLoading, error } = usePlan(planId)
   const { identity } = useCompanyIdentity(planId)
   const { analysis } = useStrategicAnalysis(planId)
   const { tools } = useAnalysisTools(planId)
   const { strategies } = useStrategies(planId)
   const { success, error: showError } = useToast()

  // Determinar la pestaña activa basada en la URL
  const getActiveTab = () => {
    const path = location.pathname
    if (path.includes('/identidad')) return 'identity'
    if (path.includes('/cadena-valor')) return 'value-chain'
    if (path.includes('/swot')) return 'swot'
    if (path.includes('/herramientas')) return 'tools'
    if (path.includes('/estrategias')) return 'strategies'
    if (path.includes('/usuarios')) return 'users'
    return 'identity' // default
  }

  const activeTab = getActiveTab()

  const handleSectionSave = async (section, data) => {
    try {
      console.log(`Saving ${section}:`, data)
      await new Promise(resolve => setTimeout(resolve, 500))
      return Promise.resolve()
    } catch (err) {
      throw new Error('Error al guardar los datos')
    }
  }

  const tabs = [
    {
      id: 'identity',
      title: 'Identidad Empresarial',
      path: `/plan/${planId}/identidad`,
      icon: Building,
      component: IdentityEditor
    },
    {
      id: 'value-chain',
      title: 'Cadena de Valor Interna',
      path: `/plan/${planId}/cadena-valor`,
      icon: LinkIcon,
      component: ValueChainEditor
    },
    {
      id: 'swot',
      title: 'Análisis SWOT',
      path: `/plan/${planId}/swot`,
      icon: TrendingUp,
      component: SwotEditor
    },
    {
      id: 'tools',
      title: 'Herramientas de Análisis',
      path: `/plan/${planId}/herramientas`,
      icon: BarChart3,
      component: AnalysisToolsEditor
    },
    {
      id: 'strategies',
      title: 'Estrategias',
      path: `/plan/${planId}/estrategias`,
      icon: Target,
      component: StrategiesEditor
    },
    {
      id: 'users',
      title: 'Usuarios',
      path: `/plan/${planId}/usuarios`,
      icon: Users,
      component: null // Will handle inline
    }
  ]

  // Function to calculate completion percentage for each section
  const calculateSectionCompletion = (sectionId) => {
    switch (sectionId) {
      case 'identity':
        if (!identity) return 0
        const identityFields = [identity.mission, identity.vision, identity.values?.length > 0, identity.general_objectives?.length > 0]
        const identityCompleted = identityFields.filter(Boolean).length
        return Math.round((identityCompleted / identityFields.length) * 100)

      case 'value-chain':
        if (!tools) return 0
        const valueChainFields = [
          Object.keys(tools.value_chain_primary || {}).length > 0,
          Object.keys(tools.value_chain_support || {}).length > 0
        ]
        const valueChainCompleted = valueChainFields.filter(Boolean).length
        return Math.round((valueChainCompleted / valueChainFields.length) * 100)

      case 'swot':
        if (!analysis) return 0
        const swotFields = [
          analysis.internal_strengths?.length > 0,
          analysis.internal_weaknesses?.length > 0,
          analysis.external_opportunities?.length > 0,
          analysis.external_threats?.length > 0
        ]
        const swotCompleted = swotFields.filter(Boolean).length
        return Math.round((swotCompleted / swotFields.length) * 100)

      case 'tools':
        if (!tools) return 0
        const toolsFields = [
          Object.keys(tools.participation_matrix || {}).length > 0,
          tools.porter_competitive_rivalry,
          tools.porter_supplier_power,
          tools.porter_buyer_power,
          tools.porter_threat_substitutes,
          tools.porter_threat_new_entrants,
          tools.pest_political,
          tools.pest_economic,
          tools.pest_social,
          tools.pest_technological
        ]
        const toolsCompleted = toolsFields.filter(Boolean).length
        return Math.round((toolsCompleted / toolsFields.length) * 100)

      case 'strategies':
        if (!strategies) return 0
        const strategiesFields = [
          strategies.strategy_identification?.length > 0,
          strategies.game_growth?.length > 0,
          strategies.game_avoid?.length > 0,
          strategies.game_merge?.length > 0,
          strategies.game_exit?.length > 0,
          strategies.priority_strategies?.length > 0,
          Object.keys(strategies.implementation_timeline || {}).length > 0
        ]
        const strategiesCompleted = strategiesFields.filter(Boolean).length
        return Math.round((strategiesCompleted / strategiesFields.length) * 100)

      default:
        return 0
    }
  }

  if (isLoading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <LoadingSpinner size="large" text="Cargando plan estratégico..." />
      </div>
    )
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-red-600 mb-4">Error</h2>
          <p className="text-gray-600 mb-8">No se pudo cargar el plan estratégico.</p>
          <Link to="/dashboard" className="btn-primary">
            Volver al Dashboard
          </Link>
        </div>
      </div>
    )
  }

  const ActiveComponent = tabs.find(tab => tab.id === activeTab)?.component

  // Componente inline para gestión de usuarios
  const UsersManager = () => {
    const { users, isLoading, inviteUser, removeUser, isInviting, isRemoving } = usePlanUsers(planId)
    const [inviteEmail, setInviteEmail] = useState('')
    const [emailError, setEmailError] = useState('')

    const validateEmail = (email) => {
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
      return emailRegex.test(email)
    }

    const handleInviteUser = async (e) => {
      e.preventDefault()
      setEmailError('')

      if (!inviteEmail.trim()) {
        setEmailError('El email es requerido')
        return
      }

      if (!validateEmail(inviteEmail)) {
        setEmailError('Por favor ingresa un email válido')
        return
      }

      // Check if user is already in the plan
      const existingUser = users.find(user => user.user.email === inviteEmail)
      if (existingUser) {
        setEmailError('Este usuario ya tiene acceso al plan')
        return
      }

      try {
        await inviteUser(inviteEmail.trim())
        setInviteEmail('')
      } catch (error) {
        // Error is handled by the hook
      }
    }

    const handleRemoveUser = async (userId) => {
      if (window.confirm('¿Estás seguro de que quieres remover a este usuario del plan?')) {
        try {
          await removeUser(userId)
        } catch (error) {
          // Error is handled by the hook
        }
      }
    }

    const getUserRoleLabel = (role) => {
      switch (role) {
        case 'owner':
          return 'Propietario'
        case 'member':
          return 'Miembro'
        case 'pending':
          return 'Pendiente'
        default:
          return role
      }
    }

    const getUserRoleColor = (role) => {
      switch (role) {
        case 'owner':
          return 'text-purple-600 bg-purple-100'
        case 'member':
          return 'text-green-600 bg-green-100'
        case 'pending':
          return 'text-yellow-600 bg-yellow-100'
        default:
          return 'text-gray-600 bg-gray-100'
      }
    }

    return (
      <div className="space-y-6">
        {/* Invitar usuarios */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title flex items-center">
              <Mail className="h-5 w-5 mr-2" />
              Invitar Usuarios
            </h3>
            <p className="card-description">
              Invita a otros usuarios registrados para colaborar en este plan estratégico.
            </p>
          </div>
          <div className="card-content">
            <form onSubmit={handleInviteUser} className="flex space-x-3">
              <div className="flex-1">
                <input
                  type="email"
                  value={inviteEmail}
                  onChange={(e) => setInviteEmail(e.target.value)}
                  placeholder="usuario@email.com"
                  className="input w-full"
                  disabled={isInviting}
                />
                {emailError && (
                  <p className="form-error mt-1">{emailError}</p>
                )}
              </div>
              <button
                type="submit"
                className="btn-primary"
                disabled={isInviting || !inviteEmail.trim()}
              >
                {isInviting ? (
                  <LoadingSpinner size="small" text="" />
                ) : (
                  <>
                    <Mail className="h-4 w-4 mr-2" />
                    Invitar
                  </>
                )}
              </button>
            </form>
          </div>
        </div>

        {/* Lista de usuarios */}
        <div className="card">
          <div className="card-header">
            <h3 className="card-title flex items-center">
              <Users className="h-5 w-5 mr-2" />
              Usuarios del Plan
            </h3>
            <p className="card-description">
              Usuarios que tienen acceso a este plan estratégico.
            </p>
          </div>
          <div className="card-content">
            {isLoading ? (
              <div className="flex justify-center py-8">
                <LoadingSpinner size="medium" text="Cargando usuarios..." />
              </div>
            ) : users.length === 0 ? (
              <div className="text-center py-8">
                <Users className="h-12 w-12 text-gray-400 mx-auto mb-4" />
                <p className="text-gray-500">No hay usuarios en este plan aún.</p>
              </div>
            ) : (
              <div className="space-y-3">
                {users.map((planUser) => (
                  <div
                    key={planUser.id}
                    className="flex items-center justify-between p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors"
                  >
                    <div className="flex items-center space-x-3">
                      <div className="w-10 h-10 bg-gray-200 rounded-full flex items-center justify-center">
                        <span className="text-sm font-medium text-gray-600">
                          {planUser.user.first_name?.[0] || planUser.user.email[0].toUpperCase()}
                        </span>
                      </div>
                      <div>
                        <div className="flex items-center space-x-2">
                          <p className="font-medium text-gray-900">
                            {planUser.user.first_name && planUser.user.last_name
                              ? `${planUser.user.first_name} ${planUser.user.last_name}`
                              : planUser.user.email
                            }
                          </p>
                          {planUser.role === 'owner' && (
                            <Crown className="h-4 w-4 text-purple-500" />
                          )}
                        </div>
                        <p className="text-sm text-gray-600">{planUser.user.email}</p>
                      </div>
                    </div>
                    <div className="flex items-center space-x-3">
                      <span className={`px-2 py-1 text-xs font-medium rounded-full ${getUserRoleColor(planUser.role)}`}>
                        {getUserRoleLabel(planUser.role)}
                      </span>
                      {planUser.role !== 'owner' && (
                        <button
                          onClick={() => handleRemoveUser(planUser.user.id)}
                          className="text-red-500 hover:text-red-700 p-1 rounded transition-colors"
                          disabled={isRemoving}
                          title="Remover usuario"
                        >
                          <UserMinus className="h-4 w-4" />
                        </button>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center space-x-4 mb-4">
          <Link
            to="/dashboard"
            className="flex items-center text-gray-600 hover:text-gray-900 transition-colors"
          >
            <ArrowLeft className="h-4 w-4 mr-1" />
            Volver
          </Link>
          <div className="flex-1">
            <h1 className="text-3xl font-bold text-gray-900">{plan?.title}</h1>
            {plan?.description && (
              <p className="text-gray-600 mt-2">{plan.description}</p>
            )}
          </div>
          <Link
            to={`/plan/${planId}/resumen`}
            className="btn-secondary"
          >
            <Eye className="h-4 w-4 mr-2" />
            Ver Resumen
          </Link>
        </div>
      </div>

      {/* Navigation Tabs */}
      <div className="mb-8">
        <div className="border-b border-gray-200">
          <nav className="-mb-px flex space-x-8 overflow-x-auto">
            {tabs.map((tab) => {
              const Icon = tab.icon
              return (
                <Link
                  key={tab.id}
                  to={tab.path}
                  className={`flex items-center space-x-2 py-2 px-1 border-b-2 font-medium text-sm whitespace-nowrap ${
                    activeTab === tab.id
                      ? 'border-blue-500 text-blue-600'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                >
                  <Icon className="h-4 w-4" />
                  <span>{tab.title}</span>
                </Link>
              )
            })}
          </nav>
        </div>
      </div>

      {/* Active Tab Content */}
      <div className="space-y-8">
        {activeTab === 'users' ? (
          <UsersManager />
        ) : ActiveComponent ? (
          <ActiveComponent
            planId={planId}
            onSave={(data) => handleSectionSave(activeTab, data)}
          />
        ) : null}
      </div>

      {/* Progress Indicator */}
      <div className="mt-12 p-6 bg-gray-50 rounded-lg">
        <h3 className="text-lg font-medium text-gray-900 mb-4">Progreso del Plan</h3>
        <div className="grid grid-cols-2 md:grid-cols-6 gap-4">
          {tabs.filter(tab => tab.id !== 'users').map((tab) => {
            const completion = calculateSectionCompletion(tab.id)
            return (
              <div
                key={tab.id}
                className="p-3 rounded-lg text-center bg-white border"
              >
                <div className="text-sm font-medium text-gray-900">{tab.title}</div>
                <div className="mt-2">
                  <div className="w-full bg-gray-200 rounded-full h-2">
                    <div
                      className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                      style={{ width: `${completion}%` }}
                    ></div>
                  </div>
                  <div className="text-xs text-gray-600 mt-1">{completion}% completado</div>
                </div>
              </div>
            )
          })}
        </div>
      </div>
    </div>
  )
}

export default PlanEditor
